<template>
  <div>
    <nav>
      <div class="nav nav-tabs" id="nav-tab" role="tablist">
        <button
          v-for="(tab, index) in tabs"
          :key="index"
          :class="{ 'nav-link': true, active: index === activeTabIndex }"
          :id="'nav-' + tab.id + '-tab'"
          data-bs-toggle="tab"
          :data-bs-target="'#nav-' + tab.id"
          type="button"
          role="tab"
          :aria-controls="'nav-' + tab.id"
          :aria-selected="index === activeTabIndex"
          @click="changeTab(index)"
        >
          {{ tab.name }}
        </button>
      </div>
    </nav>

    <div class="tab-content" id="nav-tabContent">
      <!-- 각 탭 내용 -->
      <div
        v-for="(tab, index) in tabs"
        :key="index"
        :id="'nav-' + tab.id"
        class="tab-pane fade"
        :class="{ 'show active': index === activeTabIndex }"
        :role="'tabpanel'"
        :aria-labelledby="'nav-' + tab.id + '-tab'"
      >
        <div class="container">
          <div class="row">
            <template v-if="tab.id === 'home'">
              <B_Ordcard :bnum="bnum" />
            </template>
            <template v-else>
              <B_OrdSpecCard :bnum="bnum" :orderStatus="tab.orderStatus" />
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import B_OrdSpecCard from "@/components/business/MS_B_OrdSpecCard.vue";
import B_Ordcard from "@/components/business/MS_B_AllOrdcard.vue";

export default {
  name: "B_Ordering",
  components: {
    B_OrdSpecCard,
    B_Ordcard,
  },
  data() {
    return {
      tabs: [
        { id: "home", name: "전체", orderStatus: null },
        { id: "OrderReady", name: "주문수락대기", orderStatus: 802 },
        { id: "Preparing", name: "조리중", orderStatus: 803 },
        { id: "Start", name: "배달시작", orderStatus: 804 },
        { id: "contact", name: "배달완료", orderStatus: 805 },
      ],
      activeTabIndex: 0,
      bnum: "1001", // 로그인한 사용자의 사업자 번호
    };
  },
  methods: {
    changeTab(index) {
      this.activeTabIndex = index;
    },
  },
};
</script>